@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .dashboard-card {
    @apply text-center p-12 drop-shadow-md hover:drop-shadow-2xl ease-in duration-200 bg-blue-200 rounded-xl;
  }

  .button {
    @apply rounded-full bg-blue-300 dark:bg-gray-400 p-2 w-full hover:bg-blue-400 dark:hover:bg-gray-600 ease-in duration-200 focus:ring focus:ring-white;
  }

  .wrapper {
    @apply flex justify-center items-center h-dvh w-screen bg-gradient-to-br from-blue-200 to-blue-300 dark:from-gray-800 dark:to-gray-600;
  } 

  .alert-info {
    @apply bg-blue-400;
  }

  .alert-success {
    @apply bg-green-200;
  }

  .alert-warning {
    @apply bg-orange-200;
  }

  .alert-error {
    @apply bg-red-200;
  }
}
